body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.font {
  font-family: 'Montserrat', sans-serif;
  background-color: #E0E2DB;
  height: 100vh;
}

.header {
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  padding: 60px 20px 20px 20px;
}

.name-row {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100px;
}

.name {
  position: relative;
  display: inline-block;
  float: left;
  padding: 50px;
}

.button {
  background-color: #D2D4C8;
  margin: 10px;
  font-size: 20px;
  font-weight: 1000;
  color: #5F7470;
  padding: 10px 30px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 2.5px 2.5px 5px grey;
}

.pokemon-row {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100px;
}

.pokemon {
  position: relative;
  display: inline-block;
  float: left;
  padding: 40px;
}

.button-row {
  margin: 25px 0px 0px 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 50px;
}

.score {
  text-align: center;
  padding: 45px;
  font-size: 30px;
}

.outcome {
  text-align: center;
  font-size: 25px;
  padding: 10px;
}

.message {
  text-align: center;
  font-size: 20px;
  padding: 0px 0px 10px 0px;
}

